.lms_staff_card {
  width: 100%;
  background-color: #fff;
  border: 0.5px solid #cacaca;
  border-radius: 6px;
  color: #1c023b;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  margin-top: 0.5vw;
  padding: 1vw;
  position: relative;
}
.lms_staff_card_contatiner {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
}
.lms_staff_card_contatiner > img,
.lms_staff_card_contatiner > a > img {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
}

.lms_staff_card_contatiner_inner > h6 {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #121212;
  margin-bottom: 0.2rem;
}
.lms_staff_card_contatiner_inner > p {
  font-weight: 400;
  font-size: 0.878vw;
  line-height: 1.098vw;
  color: rgba(0, 0, 0, 0.6);
  margin-bottom: 0;
}
.lms_rule_container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}

/* // request card style  */

.lmsr_card {
  width: 100%;
  background-color: #fff;
  border: 0.5px solid #cacaca;
  border-radius: 6px;
  color: #1c023b;
  padding: 1rem;
  position: relative;
  margin-bottom: 0.8rem;
}
.lmsrc_card {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.lmsrc_card_inner {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
}
.lmsrc_card_inner > img {
  height: 3rem;
  width: 3rem;
  border-radius: 6px;
}
.lmsrci_text > h6 {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  margin-bottom: 0.2rem;
  color: #121212;
}
.lmsrci_text > p {
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
  margin: 0;
  color: rgba(18, 18, 18, 0.7);
}
.lmsrc_date {
  margin-left: 5px;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #0a66c2;
  cursor: pointer;
}
.lmsrc_btn {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
}
.lmsat_container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2%;
}
.lmsat_container > div {
  flex: 1 1 32%;
}
/* // for attendance  */
.lmsstlc_card {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1.5rem;
  border-bottom: 0.5px solid #efeeee;
  padding-block: 0.6rem;
}
.lmsstlc_card > img:nth-of-type(1) {
  height: 1rem;
  width: auto;
}
.lmsstlc_card > img:nth-of-type(2) {
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 50%;
}
.lmsstlc_card > p {
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
  margin: 0;
  color: rgba(18, 18, 18, 0.7);
}
.lmsstlc_card > h6 {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  margin-bottom: 0;
  color: #121212;
}
